export interface IUserInfoBase {
    type: "user" | "system" | "guest"
}
export interface IUserInfo extends IUserInfoBase {
    id: string;
    type: "user";
    email?: string;
    sessionId?: string;
}
export interface IGuestInfo extends IUserInfoBase {
    [key: string]: string;
    type: "guest";
    email: string;
}
export interface ISystemInfo extends IUserInfoBase {
    id: "jobs" | "aiService";
    type: "system";
}